import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'// Importamos home aqui ya que es la página inicial i tenemos dos Const routes de home.


Vue.use(VueRouter)

const routes = [// Creación de la barra de navegación y sus rutas entre diversas páginas
  {
  path: '/',
  name: 'Home',
  component: Home
},
{
  path: '/Home',
  name: 'Home',
  component: Home
},
  {
    path: '/Gallery Collage',
    name: 'Gallery Collage',
    component: () => import( '../views/Gallery Collage.vue') 
  }, 
 
   { 
    path: '/Visit',
    name: 'Visit',
    component: () => import ('../views/Visit.vue')
  }, 
  { 
    path: '/Contacto',
    name: 'Contact',
    component: () => import( '../views/Contacto.vue')
  }
]

const router = new VueRouter({
  mode: "history",
  base: '/gallery-studio/',
  routes
})

export default router
